import React from 'react'
import { useTranslation } from 'react-i18next'
import { useWeapon, useWeaponActions } from '~/app/hooks/weapon'
import Select from '../common/Select'

require('./WeaponSlots.css')

interface Props {
}

const WeaponSlots: React.FC<Props> = () => {
  const { t } = useTranslation()
  const { slots } = useWeapon()
  const { setSlots } = useWeaponActions()
  const value = slots.join('-')

  const onChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const [s1, s2, s3] = e.currentTarget.value.split('-').map(Number)
    setSlots([s1, s2, s3])
  }

  return (
    <div className="WeaponSlots">
      <div>{t('')}</div>
      <Select value={value} onChange={onChange}>
        <optgroup label={t('')}>
          <option value="0-0-0">{t('')}</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 1 })}>
          <option value="1-0-0">1</option>
          <option value="1-1-0">11</option>
          <option value="1-1-1">111</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 2 })}>
          <option value="2-0-0">2</option>
          <option value="2-1-0">21</option>
          <option value="2-1-1">211</option>
          <option value="2-2-0">22</option>
          <option value="2-2-1">221</option>
          <option value="2-2-2">222</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 3 })}>
          <option value="3-0-0">3</option>
          <option value="3-1-0">31</option>
          <option value="3-1-1">311</option>
          <option value="3-2-0">32</option>
          <option value="3-2-1">321</option>
          <option value="3-2-2">322</option>
          <option value="3-3-0">33</option>
          <option value="3-3-1">331</option>
          <option value="3-3-2">332</option>
          <option value="3-3-3">333</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 4 })}>
          <option value="4-0-0">4</option>
          <option value="4-1-0">41</option>
          <option value="4-1-1">411</option>
          <option value="4-2-0">42</option>
          <option value="4-2-1">421</option>
          <option value="4-2-2">422</option>
          <option value="4-3-0">43</option>
          <option value="4-3-1">431</option>
          <option value="4-3-2">432</option>
          <option value="4-3-3">433</option>
          <option value="4-4-0">44</option>
          <option value="4-4-1">441</option>
          <option value="4-4-2">442</option>
          <option value="4-4-3">443</option>
          <option value="4-4-4">444</option>
        </optgroup>
      </Select>
    </div>
  )
}

export default WeaponSlots
