import { ActiveSkill } from '~/app/modules/activeSkill'
import seriesSkill from '~/generated/seriesSkill.json'
import { fromEntries } from '../array'

export default (skill: ActiveSkill) => {
  // clone
  skill = { ...skill }

  // 
  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 1) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 5) {
    skill[''] = 1
  }

  if (skill[''] > 5) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  if (skill[''] > 3) {
    skill[''] = 1
  }

  // 
  skill = fromEntries(
    Object.entries(skill).map(([name, value]) =>
      [name in seriesSkill ? `_${name}` : name, value]
    )
  )

  return skill
}
